// Fonts
$font-main: 'inter', sans-serif;

// Colors 
$black: #1d1e25;
$primary: #F5F5FF;
$white: #fff;
$blur-orange: #FA7515;
$blur-green: #E2F8F0;
$grey: #7e8391;
// $grey-light: #ededed;
$grey-light: #d0cfcf;
$blue: #1463FF;
$ap: clamp(0.5px, 100vw / 1200, 1px); 

.button {
  padding: calc($ap * 16) calc($ap * 44);
  border-radius: 30px;
  background-color: $black;
  color: $white;
  font-weight: 700;
  white-space: nowrap;
  font-size: max(10px, calc($ap * 16));
  line-height: 1.5;
  transition: all 0.3s ease;
}

.button:hover {
  background-color: $white;
  color: $black;
  outline: 1px solid $grey-light;
}

.line2 {
  border: 1px solid #ededed;
  width: 100%;
  height: 1px;
  margin: 0 auto;
}

.title-72 {
  font-family: Inter;
  font-style: normal;
  font-weight: 700;
  font-size: calc($ap * 72);
  line-height: 1.2;
  letter-spacing: -3px;
  text-align: left;
  color: #1d1e25;
}

.title-h2 {
  font-family: $font-main;
  font-style: normal;
  font-weight: 600;
  font-size: calc($ap * 56);
  line-height: 1.2;
  letter-spacing: -2px;
}

.title-h3 {
  font-family: $font-main;
  font-style: normal;
  font-weight: 600;
  font-size: calc($ap * 32);
  line-height: 1.31;
  letter-spacing: -1px;
}

.desk-p18 {
  font-family: $font-main;
  font-style: normal;
  font-weight: 500;
  font-size: max(13px, calc($ap * 18));
  line-height: 1.5;
  color: $grey;
}

.desk-p16 {
  font-family: $font-main;
  font-style: normal;
  font-weight: 500;
  font-size: max(12px, calc($ap * 16));
  line-height: 1.62;
  color: #7e8391;
}

.font-24 {
  font-weight: 700;
  font-size: calc($ap * 24);
  line-height: 1.33;
}